import { Component, OnInit } from '@angular/core';
import {USA_ALL} from "./us-all";
import {StorageService} from "../../services/storage.service";
import {Router} from "@angular/router";

declare var $:any;

@Component({
  selector: 'us-states',
  templateUrl: './us-states.component.html',
  styleUrls: ['./us-states.component.scss']
})
export class UsStatesComponent implements OnInit {

    public data : any = MapObject;
    // public data : any = dummyData;
    public us_data : any = USA_ALL;
    constructor( public storageService: StorageService,
                    public router: Router) {
    }

    ngOnInit() {
        this.generateMap();
    }

    generateMap(){
      Highcharts.mapChart('container', {
          chart: {
              map: this.us_data
          },
          credits: {
              enabled: false
          },

          title: {
              text: 'US State Import Export'
          },

          mapNavigation: {
              enabled: true,
              buttonOptions: {
                  align:'right',
                  verticalAlign: 'top'
              }
          },

          legend: {
              layout: 'vertical',
              borderWidth: 0,
              // backgroundColor: 'rgba(255,255,255,0.85)',
              floating: true,
              verticalAlign: 'top',
              align: 'right',
              y: 100
          },

          colorAxis: {
              min: 0
          },

          plotOptions: {
              series: {
                  events: {
                      click: (e)=> {
                          this.stateClickHandler(e);
                      }
                  }
              }
          },

          series: [{
              data: this.data,
              mapData: Highcharts.geojson(this.us_data),
              name: 'Foreign Trade',
              states: {
                  hover: {
                      color: '#BADA55'
                  }
              },
              tooltip: {
                  formatter: function() {
                      return '<b>Series name: ' + this.series.name + '</b><br>' +
                          'Point name: ' + this.point.name + '<br>' +
                          'Pont Value : ' + this.point.value
                  }
              },
              dataLabels: {
                  enabled: true,
                  format: '{point.name}'
              }
          }, {
              name: 'Separators',
              type: 'mapline',
              data: Highcharts.geojson(this.us_data, 'mapline'),
              color: 'silver',
              showInLegend: false,
              enableMouseTracking: false
          }]
      });
    }

    stateClickHandler(event){
        this.storageService.setScope(event.point.name);
        this.router.navigate(['charts']);
    }

    getHcCode(){
        let countryData = [];
        this.us_data.features.forEach((singleC, index)=>{
            countryData.push({
                name : singleC.properties.name,
                code : singleC.properties['hc-key'],
                value : index+10
            })
        });
        this.data = countryData;
    }

}

