import { Component, OnInit } from '@angular/core';
import {StorageService} from "../../services/storage.service";
import {Router} from "@angular/router";

@Component({
  selector: 'app-charts',
  templateUrl: './charts.component.html',
  styleUrls: ['./charts.component.scss']
})
export class ChartsComponent implements OnInit {

    public years : [any] = ["2013","2014","2015","2016"];

    public currentTab : string = "country";

    public countryExportChart : any;
    public countryImportsChart : any;
    public stateExportsChart : any;
    public stateImportsChart: any;

    public country : any = {
        exports : {},
        imports : {}
    };
    public state : any = {
        exports : {},
        imports : {}
    };

    public selectedYear : any = "2016";

    public model: any = {
      category : 'imports'
    };

    public statesInUs : any[];
    public products : any[];

    constructor(public storageService : StorageService,
                public router :Router) {
      this.statesInUs = storageService.getAllStates()
    }

    optionChange(event){
        let selectedYear = event.target.value;
        this.countryExportChart.series[0].setData(this.country.exports.require.years[selectedYear]);
        this.countryImportsChart.series[0].setData(this.country.imports.require.years[selectedYear]);
        this.stateExportsChart.series[0].setData(this.state.exports.require.years[selectedYear]);
        this.stateImportsChart.series[0].setData(this.state.imports.require.years[selectedYear]);
    }

    ngOnInit() {
        let stateName = this.storageService.getScope();
        if(stateName){
            this.constructMapData(stateName);
            this.generateCountryExpCharts();
            this.generateCountryImpCharts();
            this.generateStateExpCharts();
            this.generateStateImpCharts();
        }
        else {
            this.router.navigate(['']);
        }
    }

    constructMapData(stateName){
        this.country.exports.data =  this.storageService.getCountryExports(stateName);
        this.country.exports.require = this.getRequireData(this.country.exports.data);
        this.country.imports.data =  this.storageService.getCountryImports(stateName);
        this.country.imports.require = this.getRequireData(this.country.imports.data);
        this.state.exports.data =  this.storageService.getStateExports(stateName);
        this.state.exports.require = this.collectStateData(this.state.exports.data);
        this.state.imports.data =  this.storageService.getStateImports(stateName);
        this.state.imports.require = this.collectStateData(this.state.imports.data);
    }

    getRequireData(data){
        let actualData = {
            countryd : [],
            years : {
                "2013" : [],
                "2014" : [],
                "2015" : [],
                "2016" : [],
            }
        };
        data.forEach((node)=>{
            actualData.countryd.push(node.countryd);
            actualData.years["2013"].push(node.val2013);
            actualData.years["2014"].push(node.val2014);
            actualData.years["2015"].push(node.val2015);
            actualData.years["2016"].push(node.val2016);
        });
        return actualData;
    }

    collectStateData(stateData){
        let stateActualData = {
            products : [],
            years : {
                "2013" : [],
                "2014" : [],
                "2015" : [],
                "2016" : [],
            }
        };
        stateData.forEach((node)=>{
            stateActualData.products.push(node.productName);
            stateActualData.years["2013"].push(node.val2013);
            stateActualData.years["2014"].push(node.val2014);
            stateActualData.years["2015"].push(node.val2015);
            stateActualData.years["2016"].push(node.val2016);
        });
        console.log(stateActualData);
        console.log(stateData);
        return stateActualData;
    }

    generateCountryExpCharts(){
        this.countryExportChart =  Highcharts.chart('countryExpChart', {
          chart: {
            type: 'column',
            plotBorderWidth: 1,
            marginTop: 100
          },
          title: {
            text: 'Country Export Chart',
            y:40
          },
          credits: {
            enabled: false
          },
          xAxis: {
            categories: this.country.exports.require.countryd,
            title: {
              text: null
            }
          },
          yAxis: {
            min: 0,
            title: {
              text: 'Amount (Millions Dollar)'
            },
            labels: {
              overflow: 'justify'
            }
          },
          tooltip: {
            valueSuffix: ' millions'
          },
          plotOptions: {
            bar: {
              dataLabels: {
                enabled: true
              }
            }
          },
          legend: {
            align: 'right',
            verticalAlign: 'top',
            x: -10,
            y: 0,
            floating: true,
            borderWidth: 1,
            backgroundColor: ((Highcharts.theme && Highcharts.theme.legendBackgroundColor) || '#FFFFFF'),
            shadow: true
          },
          series: [ {
            name: 'Exports',
            // data: [133, 156, 947, 408, 60]
            data: this.country.exports.require.years[this.selectedYear]
          }]
        });
    }

    generateCountryImpCharts(){
        this.countryImportsChart =  Highcharts.chart('countryImpChart', {
            chart: {
                type: 'column',
                plotBorderWidth: 1,
                marginTop: 100
            },
            title: {
                text: 'Country Import Chart',
                y:40
            },
            credits: {
                enabled: false
            },
            xAxis: {
                categories: this.country.imports.require.countryd,
                title: {
                    text: null
                }
            },
            yAxis: {
                min: 0,
                title: {
                    text: 'Amount (Millions Dollar)'
                },
                labels: {
                    overflow: 'justify'
                }
            },
            tooltip: {
                valueSuffix: ' millions'
            },
            plotOptions: {
                bar: {
                    dataLabels: {
                        enabled: true
                    }
                }
            },
            legend: {
                align: 'right',
                verticalAlign: 'top',
                x: -10,
                y: 0,
                floating: true,
                borderWidth: 1,
                backgroundColor: ((Highcharts.theme && Highcharts.theme.legendBackgroundColor) || '#FFFFFF'),
                shadow: true
            },
            series: [ {
                name: 'Imports',
                // data: [133, 156, 947, 408, 60]
                data: this.country.imports.require.years[this.selectedYear]
            }]
        });
    }

    generateStateExpCharts(){
      this.stateExportsChart =  Highcharts.chart('stateExpChart', {
            chart: {
                type: 'column',
                plotBorderWidth: 1,
                marginTop: 100
            },
            title: {
                text: 'State Export Chart',
                y:40
            },
            credits: {
                enabled: false
            },
            xAxis: {
                categories: this.state.exports.require.products,
                title: {
                    text: null
                }
            },
            yAxis: {
                min: 0,
                title: {
                    text: 'Amount (Millions Dollar)'
                },
                labels: {
                    overflow: 'justify'
                }
            },
            tooltip: {
                valueSuffix: ' millions'
            },
            plotOptions: {
                bar: {
                    dataLabels: {
                        enabled: true
                    }
                }
            },
            legend: {
                align: 'right',
                verticalAlign: 'top',
                x: -10,
                y: 0,
                floating: true,
                borderWidth: 1,
                backgroundColor: ((Highcharts.theme && Highcharts.theme.legendBackgroundColor) || '#FFFFFF'),
                shadow: true
            },
            series: [ {
                name: 'Exports',
                // data: [133, 156, 947, 408, 60]
                data: this.state.exports.require.years[this.selectedYear]
            }]
        });
    }

    generateStateImpCharts(){
        this.stateImportsChart =  Highcharts.chart('stateImpChart', {
            chart: {
                type: 'column',
                plotBorderWidth: 1,
                marginTop: 100
            },
            title: {
                text: 'State Import Chart',
                y:40
            },
            credits: {
                enabled: false
            },
            xAxis: {
                categories: this.state.imports.require.products,
                title: {
                    text: null
                }
            },
            yAxis: {
                min: 0,
                title: {
                    text: 'Amount (Millions Dollar)'
                },
                labels: {
                    overflow: 'justify'
                }
            },
            tooltip: {
                valueSuffix: ' millions'
            },
            plotOptions: {
                bar: {
                    dataLabels: {
                        enabled: true
                    }
                }
            },
            legend: {
                align: 'right',
                verticalAlign: 'top',
                x: -10,
                y: 0,
                floating: true,
                borderWidth: 1,
                backgroundColor: ((Highcharts.theme && Highcharts.theme.legendBackgroundColor) || '#FFFFFF'),
                shadow: true
            },
            series: [{
                name: 'Imports',
                // data: [1052, 954, 4250, 740, 38]
                data: this.state.imports.require.years[this.selectedYear],
            }]
        });
    }

    tabChange(event, value){
        this.currentTab = value
    }

}



