import { Injectable } from '@angular/core';
import {USA_ALL} from "../common/us-states/us-all";
import {ProductsInfo, CountryExports, CountryImports, StateExports, StateImports} from "./data.service";

@Injectable()
export class StorageService {

    public scope: any;
    public us_data : any = USA_ALL;
    public products : any = ProductsInfo;
    public countryImports : any = CountryImports;
    public countryExports : any = CountryExports;
    public stateExports : any = StateExports;
    public stateImports : any = StateImports;

    constructor() {
    }

    public getScope(): any {
        return this.scope;
    }

    public setScope(scope: any): any {
        this.scope = scope;
    }

    public getAllStates(){
        let statesArray = [];
        this.us_data.features.forEach((singleState)=>{
            statesArray.push(singleState.properties.name);
        });
        return statesArray
    }

    getCountryImports(stateName){
        return this.countryImports.filter((expDetail)=>{
                return expDetail.name.toUpperCase() === stateName.toUpperCase()
            }) || [];
    }

    getCountryExports(stateName){
        return this.countryExports.filter((expDetail)=>{
            return expDetail.name.toUpperCase() === stateName.toUpperCase()
        }) || []
    }

    getStateImports(stateName){
        return this.stateImports.filter((impDetail)=>{
            return impDetail.name.toUpperCase() === stateName.toUpperCase()
        }) || []
    }

    getStateExports(stateName){
        return this.stateExports.filter((expDetail)=>{
                return expDetail.name.toUpperCase() === stateName.toUpperCase()
            }) || [];
    }

}



