import {Routes, RouterModule} from '@angular/router';
import {ModuleWithProviders} from '@angular/core';
import {UsStatesComponent} from "./common/us-states/us-states.component";
import {ChartsComponent} from "./common/charts/charts.component";


export const APP_ROUTES: Routes = [
    {
        path: '',
        component: UsStatesComponent
    },
    {
        path: 'charts',
        component: ChartsComponent
    },
    {
        path: '**',
        redirectTo: '',
        pathMatch: 'full'
    }
];

export const AppRoutesModule: ModuleWithProviders = RouterModule.forRoot(APP_ROUTES);





